@import url("https://fonts.googleapis.com/css?family=Noto+Sans+TC:400,700&display=swap");
* {
  box-sizing: border-box; }

[v-cloak] {
  display: none; }

::-webkit-scrollbar {
  width: 10px; }

::-webkit-scrollbar-track {
  background: white;
  border: 3px solid #646464;
  border-radius: 100px; }

::-webkit-scrollbar-thumb {
  background: #646464;
  border: 3px solid #646464;
  border-radius: 100px; }

::-webkit-scrollbar-thumb:hover {
  background: #4b4b4b; }

body {
  margin: 0;
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
  font-family: 'Noto Sans TC', sans-serif;
  background-color: #FF8C6E; }

.container {
  position: relative;
  padding-top: 30px;
  height: 100%; }
  .container.slideMenu .playList {
    transform: translateX(-100%) rotate(25deg); }
    @media (max-width: 768px) {
      .container.slideMenu .playList {
        transform: translateX(-120%) rotate(25deg); } }
  .container.slideMenu .player {
    transform: translateX(0%); }

.player {
  border: 3px solid #000;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  background-color: #fff;
  position: relative;
  margin: 0 auto;
  width: 50vw;
  min-width: 450px;
  transform: translateX(45%);
  transition: transform 0.5s; }
  @media (max-width: 1200px) {
    .player {
      transform: translateX(50%); } }
  @media (max-width: 768px) {
    .player {
      transform: translateX(0%); } }
  .player:before, .player:after {
    border: 3px solid #000;
    content: '';
    position: absolute;
    display: block;
    background-color: #C5C5C5;
    border-radius: 4px; }
  .player:before {
    right: -26px;
    top: -4px;
    width: 20px;
    height: 100%;
    transform: skewY(45deg);
    transform-origin: top left; }
  .player:after {
    left: -4px;
    bottom: -26px;
    width: 100%;
    height: 20px;
    transform: skewX(45deg);
    transform-origin: top left; }

.playList {
  border: 3px solid #000;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 0;
  max-width: 40vw;
  max-height: calc(100vh - 2vh);
  display: flex;
  transition: transform 0.5s; }
  @media (max-width: 768px) {
    .playList {
      max-width: 70vw; } }
  .playList_leftSide {
    flex: 0 0 30%;
    max-width: 30%;
    padding-top: 33px;
    padding-left: 20px; }
  .playList_rightSide {
    border: 3px solid #000;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    background-color: #fff;
    position: relative;
    flex: 1 0 70%;
    max-width: 70%;
    top: 15px;
    right: -15px; }
  .playList_search {
    display: flex;
    border: 1px solid #000;
    background-color: #DFDFDF; }
    .playList_search > input {
      background-color: transparent;
      border: none;
      outline: none;
      cursor: pointer;
      width: 100%; }
    .playList_search > button {
      background-color: transparent;
      border: none;
      outline: none;
      cursor: pointer; }

.playList_cata_title {
  font-size: 12px;
  font-weight: 400;
  border-bottom: 1px solid #000;
  cursor: pointer; }

.playList_cata_list {
  list-style: none;
  padding-left: 0; }

.playList_cata_list_item > a {
  position: relative;
  display: block;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  line-height: 1.8; }
  .playList_cata_list_item > a:before {
    content: '';
    display: block;
    position: absolute;
    left: -20px;
    top: 4px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7px 0 7px 13px;
    border-color: transparent transparent transparent #000;
    opacity: 0;
    transition: .3s; }

.playList_cata_list_item:hover > a:before, .playList_cata_list_item.active > a:before {
  content: '';
  opacity: 1; }

.playList_rightSide {
  display: flex;
  flex-direction: column; }
  .playList_rightSide_top, .playList_rightSide_middle {
    padding: 15px; }
  .playList_rightSide_top {
    display: flex;
    justify-content: space-between; }
    .playList_rightSide_top > h2 {
      margin: 0;
      font-size: 30px;
      font-weight: 400; }
  .playList_rightSide_middle {
    margin: 15px 0;
    height: 60vh;
    max-height: 60vh;
    overflow-y: auto; }
  .playList_rightSide_bottom {
    position: relative;
    padding: 10px 0;
    background-color: #000; }

.playList_switchBtn {
  color: #000;
  text-decoration: none; }
  .playList_switchBtn > span {
    display: block;
    font-weight: bold; }
  .playList_switchBtn > i {
    font-size: 32px; }

.playList_currentList {
  list-style: none;
  padding-left: 0; }
  .playList_currentList_item {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 5px;
    border-top: 3px solid #000;
    border-bottom: 3px solid #000;
    transition: .5s;
    cursor: pointer; }
    .playList_currentList_item ~ .playList_currentList_item {
      margin-top: -3px; }
    .playList_currentList_item_left {
      flex: 0 0 50%;
      max-width: 50%; }
    .playList_currentList_item_right {
      flex: 0 0 30%;
      max-width: 40%;
      margin-left: 10px;
      text-align: right; }
    .playList_currentList_item:hover {
      background-color: #C5C5C5; }

.list_songName {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  font-size: 18px;
  font-weight: bold; }

.list_artistName, .list_albumName {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  font-size: 14px; }

.list_favorite {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer; }

.ads {
  display: none; }
  .ads:first-child {
    display: block; }
  .ads_img {
    display: block;
    max-width: 100%; }

.fade {
  animation-name: fade;
  animation-duration: 0.5s; }

@keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }

#js_cdCanvas {
  position: absolute;
  top: 0;
  left: 0; }

.player_cd {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  border: 3px solid #000;
  position: relative;
  margin: 0 auto;
  width: 500px;
  height: 500px;
  border-radius: 100%;
  overflow: hidden;
  animation: aniPlay 5s .5s infinite linear, aniShake .5s .5s infinite ease-in-out;
  animation-play-state: paused; }
  @media (max-width: 1200px) {
    .player_cd {
      width: 250px;
      height: 250px; } }
  .player_cd_cover {
    width: 100%; }
  .player_cd_circle {
    border: 3px solid #000;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 20%;
    height: 20%;
    border-radius: 100%; }
  .player_cd.aniPlay {
    animation-play-state: running; }

@keyframes aniPlay {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@keyframes aniShake {
  0%, 100% {
    top: 0; }
  50% {
    top: -3px; } }

.btn {
  border: 3px solid #000;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background-color: #fff;
  box-shadow: 3px 3px 0 0 #C5C5C5, 6px 6px 0 0 #000; }
  .btn:hover, .btn.active {
    background-color: #C5C5C5;
    transform: translate(3px, 3px);
    box-shadow: 3px 3px 0 0 #000 inset; }
  @media (max-width: 1200px) {
    .btn {
      width: 45px;
      height: 45px; }
      .btn > img {
        width: 50%; } }

.player_btn_preAndNext {
  position: absolute;
  top: 30px;
  right: 40px; }
  .player_btn_preAndNext .btn:first-child {
    margin-bottom: 15px; }

.player_btn_random {
  position: absolute;
  top: 45%;
  right: 40px; }
  .player_btn_random_dot {
    position: absolute;
    bottom: -20px;
    left: 50%;
    display: block;
    width: 8px;
    height: 8px;
    border-radius: 100%;
    background-color: #000; }

.player_controls {
  padding: 15px;
  position: relative; }
  .player_controls_displayList > h1 {
    width: 40%;
    margin: 0;
    font-size: 24px; }
    @media (max-width: 1200px) {
      .player_controls_displayList > h1 {
        font-size: 18px; } }
  .player_controls_controller {
    position: absolute;
    width: 340px;
    height: 100px;
    top: 0;
    left: 50%;
    transform: translateX(-50%); }
    @media (max-width: 1200px) {
      .player_controls_controller {
        width: 200px; } }
    .player_controls_controller > .player_control_tick {
      position: absolute;
      display: block;
      width: 100%;
      bottom: -40px;
      left: 30%;
      transform: translateX(-50%) rotate(-20deg);
      transform-origin: 75% 80%;
      transition: transform .5s ease-in-out; }
      @media (max-width: 1200px) {
        .player_controls_controller > .player_control_tick {
          bottom: -20px;
          left: 20%;
          transform-origin: 80% 80%; } }
      .player_controls_controller > .player_control_tick > img {
        max-width: 100%; }
      .player_controls_controller > .player_control_tick.active {
        transform: translateX(-50%) rotate(0deg); }
        @media (max-width: 1200px) {
          .player_controls_controller > .player_control_tick.active {
            transform: translateX(-50%) rotate(10deg); } }
      .player_controls_controller > .player_control_tick:before {
        border: 3px solid #000;
        content: '';
        position: absolute;
        display: block;
        width: 90px;
        height: 90px;
        right: 45px;
        bottom: 35px;
        border-radius: 100%;
        background-color: #C5C5C5;
        z-index: -1; }
        @media (max-width: 1200px) {
          .player_controls_controller > .player_control_tick:before {
            width: 60px;
            height: 60px;
            right: 3px;
            bottom: 23px; } }
    .player_controls_controller > button {
      background-color: transparent;
      border: none;
      outline: none;
      cursor: pointer;
      position: absolute;
      display: block;
      padding: 30px;
      left: 51%;
      top: 55%;
      transform: translate(-50%, -50%); }
      @media (max-width: 1200px) {
        .player_controls_controller > button > img {
          max-width: 85%; } }
  .player_controls_volumn {
    display: flex;
    align-items: center;
    position: absolute;
    top: 30px;
    right: 40px;
    width: 150px; }
    .player_controls_volumn .progressBar_outter {
      margin-right: 5px; }
  .player_controls_timeline {
    margin: 45px 25px 15px 0;
    display: flex;
    align-items: center; }
    .player_controls_timeline .progressBar_outter {
      margin: 0 5px; }

.slidecontainer {
  width: 100%;
  margin-right: 10px; }

.slider {
  border: 3px solid #000;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  background-color: #fff;
  appearance: none;
  width: 100%;
  height: 10px;
  border-radius: 100px;
  background-color: #fff;
  outline: none;
  transition: background-color .2s; }
  .slider:hover {
    background-color: #C5C5C5; }

.slider::-webkit-slider-thumb {
  appearance: none;
  width: 16px;
  height: 25px;
  background-color: #fff;
  border: 3px solid #000;
  cursor: pointer; }

.slider::-moz-range-thumb {
  width: 16px;
  height: 25px;
  background-color: #fff;
  border: 3px solid #000;
  cursor: pointer; }
